@import url('https://fonts.googleapis.com/css?family=Cabin|Herr+Von+Muellerhoff|Source+Sans+Pro:400,900&display=swap');
/*Global styles*/
*,
*::before,
*::after{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --main-font: 'Source Sans Pro', sans-serif;
    --secondary-font: 'Herr Von Muellerhoff', cursive;
    --body-font: 'Cabin', sans-serif;
    --main-font-color-dark: #252525;
    --secondary-font-color: #c59d5f;
    --body-font-color: #515151;
}

body{
    overflow-x: hidden;
}

html{
    font-family: var(--main-font);
    font-size: 10px;
    color: var(--body-font-color);
    scroll-behavior: smooth;
}

section{
    padding: 3.9rem 0;
}

img{
    width: 100%;
    max-width: 100%;
}

a{
    text-decoration: none;
}

p{
    font-size: 1.6rem;
}
.container{
    width:100%;
    max-width: 122.5rem;
    padding: 0 2.4rem;
    margin: 0 auto;
}

/* header */

header{
    width:100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    background-image: linear-gradient(to bottom, RGba(0,0,0,.5),transparent);
}

.nav{
    height: 7.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.menu-toggle{
    color: #fff;
    position: absolute;
    font-size: 2.2rem;
    top: 50%;
    transform: translateY(-50%);
    right:2.5rem;
    cursor: pointer;
    z-index: 1500;
}

.fa-times{
    display: none;
}

.nav-list{
    list-style: none;
    position:fixed;
    top:0;
    left:0;
    width:80vw;
    height:100vh;
    background-color:var(--main-font-color-dark);
    padding: 4.4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    z-index: 1250;
    transform: translateX(-100%);
    transition:transform 0.5s
}


.nav::before{
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.8);
    z-index: 1000;
    opacity: 0;
    transform: scale(0);
    transition: opacity 0.5s;
}

.open .fa-times{
    display:block;
}

.open .fa-bars{
    display: none;
}

.open .nav-list{
    transform: translateX(0);
}

.open .nav::before{
    opacity: 1;
    transform: scale(1);
}

.nav-item{
    border-bottom: 1px solid RGba(255,255,255,.3) ;
}

.nav-link{
    display: block;
    color:#fff;
    text-transform: uppercase;
    font-size:1.6rem;
    letter-spacing: 2px;
    margin-right: 2px;
    transition: color 0.5s;
}

.nav-link:hover{
    color: var(--secondary-font-color);
}


/* hero */

.hero{
    width: 100%;
    height: 100vh;
    background: url(images/hero.jpg) center no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    text-align: center;
}


.sub-headline{
    font-size:8.5rem ;
    font-family: var(--secondary-font);
    color: var(--secondary-font-color);
    font-weight: 100;
    line-height: .5;
    letter-spacing: 2px;
    opacity: 0;
    animation: fadeUp 0.5s forwards;
    animation-delay: .5s;
}

.sub-headline::first-letter{
    text-transform: uppercase;
    font-size: 10.3rem;
}

.headline{
    color:#fff;
    font-size: 3.7rem;
    font-family: var(--main-font);
    text-transform: uppercase;
    font-weight: 900;
    letter-spacing: 0.5rem;
    margin-right: -0.5rem;
    opacity: 0;
    animation: scale 0.5s forwards;
}

.separator{
    display: flex;
    align-items: center;
    justify-content: center;
}

.line{
    width: 100%;
    max-width: 8.4rem;
    height: 0.25rem;
    background-color: #fff;
    position: relative;
    opacity: 0;
    animation: grow 2s forwards;
    animation-delay: .9s;
}

.line-right::before,.line-left::before{
    content: '';
    position: absolute;
    top:50%;
    transform: translatey(-50%);
    border: 0.6rem solid transparent;
    opacity: 0;
    animation: grow .5s forwards;
    animation-delay: .1.2s;
}

.line-right::before{
    border-right-color: var(--secondary-font-color);
    right:0;
}

.line-left::before{
    border-left-color: var(--secondary-font-color);
    left:0;
}

.asterisk{
    font-size: 1.2rem;
    color: var(--secondary-font-color);
    margin: 0 1rem;
    opacity: 0;
    animation: spin 0.5s forwards;
    animation-delay: 0.7s;
}

.single-animation{
    opacity: 0;
    animation: fadeDown 0.5s forwards;
    animation-delay: 1.5s;
}

.headline-description h5{
    color:#fff;
    font-size: 1.4rem;
    font-weight:100;
    text-transform: uppercase;
    margin-bottom: 1.2rem;
    letter-spacing: 0.3rem;
    margin-right: -0.3rem;
}

.btn{
    display: inline-block;
    text-transform: uppercase;
    letter-spacing:2px;
    margin-right: -2px;
}

.ctn-btn{
    font-size: 1.1rem;
    background-color: #fff;
    padding: .9rem 1.8rem;
    color: var(--body-font-color);
    border-radius: 0.4rem;
    transition: background-color 0.4s;
}

.ctn-btn:hover{
    color:#fff;
    background-color: var(--secondary-font-color);
}

 /* discover-our-story  */

.global-headline{
    text-align: center;
    margin-top: 3.9rem;
}

.discover-our-story .global-headline{
margin-top: 6.9rem;
}

.global-headline .asterisk{
    color: var(--body-font-color);
    margin: 2.4rem 0;
}

.headline-dark{
    color: var(--main-font-color-dark);
    letter-spacing: .7rem;
    margin-right:-.7rem;
}

.global-headline .sub-headline{
    letter-spacing: -1px;
    line-height:.42;
}

.restaurant-info{
    text-align: center;
}

.restaurant-description{
    margin-bottom:3rem;
}

.restaurant-description p{
    letter-spacing: 1.6;
    margin-bottom: 2.4rem;
}

.body-btn{
    font-size: 1.5rem;
    color: var(--secondary-font-color);
    position: relative;
    transition: color .5s;
}

.body-btn::before{
    content: '';
    position: absolute;
    left: 0;
    bottom: -3px;
    width: 100%;
    height: 2px;
    background-color: var(--secondary-font-color);
    transition: background-color .5s;
}

.body-btn:hover,
.body-btn:hover{
    color: var(--main-font-color-dark);
}

.body-btn:hover::before,
.body-btn:hover::before{
    background-color: var(--main-font-color-dark);
}

/* <!-- tasteful recipes --> */
.tasteful-recipes{
    background:url(images/tasteful-recipes.jpg) center no-repeat;
    background-size: cover;
}

.between{
    min-height: 65vh;
    display: flex;
    align-items: center;
}

/* discover our menu */

.image-group{
    display: grid;
    grid-template-columns:repeat(2, 1fr);
    grid-gap: 1.5rem;
}

.discover-our-menu .restaurant-description{
    padding-top: 3rem;
    margin-bottom: 0;
}

/* predect blend */

.perfect-blend{
    background:url(images/perfect-blend.jpg) center no-repeat;
    background-size: cover;
}

/* footer */

footer{
    padding: 7.9rem 0;
    background-color: #121212;
    color: #fff;
    text-align: center;
    position: relative;
}

.back-to-top{
    width:7rem;
    height:7rem;
    background-color: #121212;
    position: absolute;
    top: -3rem;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 50%;
}

.back-to-top i{
    display: block;
    color: #fff;
    font-size: 2rem;
    padding: 2rem;
    animation: up 2s infinite;
}

.footer-content{
    /* overflow: hidden; */
}

.footer-content h4{
    font-size: 1.9rem;
    text-transform: uppercase;
    font-weight: 100;
    letter-spacing: 3px;
    margin-bottom: 3rem;
}

.footer-content .asterisk{
    margin: 2.4rem 0;
}

.footer-content-about{
    margin-bottom: 5.4rem;
}

.footer-content-about p{
    line-height: 2;
}

.social-icons{
    list-style: none;
    margin-bottom: 5.4rem;
    display: flex;
    justify-content: center;
}

.social-icons i{
    font-size: 2rem;
    color: #fff;
    padding: .8rem 2rem;
    opacity: .5;
    transition: color .5s;
}

.social-icons i:hover,
.social-icons i:focus{
    color: var(--secondary-font-color);
}

.newsletter-form{
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
}

.newsletter-input{
    width: 100%;
    max-width: 25rem;
    padding: 1rem;
    border-radius: .4rem;
}

.newsletter-btn{
    background-color: transparent;
    border: none;
    color:var(--main-font-color-dark);
    cursor: pointer;
    font-size: 1.6rem;
    padding: 1px .6rem;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 19.5rem;
}

/* media query */
@media screen and (min-width: 900px) {
    section{
        padding:7.9rem;
    }

    .menu-toggle{
        display: none;
    }

    .nav{
        justify-content: space-between;
    }

    .nav-list{
        position: initial;
        width: initial;
        height: initial;
        background-color: transparent;
        padding: 0;
        justify-content: initial;
        flex-direction: row;
        transform: initial;
        transition: initial;
    }

    .nav-item{
        margin: 0 2.4rem;
        border: none;
    }

    .nav-item:last-child{
        margin-right: 0;
    }

    .nav-link{
        font-size: 1.3rem;
    }

    .active{
        position: relative;
    }

    .active::before{
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #fff;
        left: 0;
        bottom: -3px;
    }

    .sub-headline{
        font-size: 10rem;
    }

    .sub-headline::first-letter{
        font-size: 12.2rem;
    }

    .headline{
        font-size: 4.7rem;
        letter-spacing: .8rem;
    }

    .line{
        max-width: 11.4rem;
    }

    .restaurant-info{
        display: flex;
        align-items: center;
    }

    .restaurant-info>div{
        flex: 1;
    }

    .padding-right{
        padding-right: 7rem;
    }

    .footer-content{
        max-width: 77.5rem;
        margin: auto;
    }

    .footer-content-about{
        max-width: 51.3rem;
        margin: 0 auto 5.4rem;
    }

    .footer-content-divider{
        display: flex;
        justify-content: space-between;
    }

    .social-media,
    .newsletter-form{
        width: 100%;
        max-width: 27.3rem;
        margin: 0 1 rem;
    }

    .social-icons i{
        opacity: 1;
    }

    .newsletter-btn{
        margin-left: 14rem;
    }
}


/* animations */

@keyframes fadeUp{
    0% {
        transform: translatey(4rem);
    }

    100% {
        opacity: 1;
        transform: translatey(0);
    }
}

@keyframes scale{
    0% {
        transform: scale(2);
    }

    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes grow{
    0% {
        width: 0;
    }

    100% {
        opacity: 1;
        width: 100%;
    }
}

@keyframes spin{
    0% {
        transform: rotate(0);
    }

    100% {
        opacity: 1;
        transform: rotate(-360deg);
    }
}

@keyframes fadeDown{
    0% {
        transform: translatey(-1rem);
    }

    100% {
        opacity: 1;
        transform: translatey(0);
    }
}

@keyframes up{
    0% {
        opacity: 0;
    }

    50%{
        opacity: 1;
    }
    100% {       
        opacity: 0;
        transform: translatey(-1rem);
    }
}